body {
  font-family: "Varela Round", sans-serif;
  background-color: lightblue;
}
h1 {
  color: red;
  text-align: center;
}
.cards {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  padding: 10px;
}
.card {
  background-color: white;
  padding: 10px;
  margin: 25px;
}
@media screen and (max-width: 450px) {
  .card {
    padding: 10px;
    margin: 5px;
  }
}

@media screen and (max-width: 450px) {
  .card img {
    width: 100px;
    height: auto;
  }
}
h2 {
  font-size: 22px;
  color: black;
  text-decoration: underline;
  text-underline-position: under;
  text-decoration-color: red;
}
@media screen and (max-width: 450px) {
  h2 {
    font-size: 15px;
  }
}
.btn {
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 10px 25px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}
@media screen and (max-width: 450px) {
  .btn {
    padding: 8px 18px;
    font-size: 12px;
    margin: 3px 1px;
  }
}
.outer {
  position: fixed;
  display: grid;
  background-color: hsla(0, 0%, 0%, 0.7);
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.2s;
}
.outer.open {
  opacity: 1;
  pointer-events: all;
}
.inner {
  max-width: 600px;
  min-width: 400px;
  min-height: 200px;
  background: white;
  padding: 2rem;
  font-size: 20px;
  border-radius: 5px;
  transform: translateY(-200%);
  transition: transform 2s;
}
@media screen and (max-width: 450px) {
  .inner {
    max-width: 400px;
    min-width: 300px;
    min-height: 200px;
    padding: 1rem;
    font-size: 13px;
    border-radius: 5px;
    transform: translateY(-100%);
    transition: transform 2s;
  }
  .inner img {
    height: 320px;
    width: auto;
  }
}
.outer.open .inner {
  transform: translateY(0%);
}
